/* Header */
header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 60px;
}

header nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 40px;
  width: 100%;
  height: 100%;
}

header nav,
header nav h1,
header nav .btn {
  margin: 0;
}

header nav .logout {
  background: transparent;
  font-size: 2.4rem;
}

header nav .logout:hover {
  text-decoration: underline;
}



