/* Channel */
.fake-header {
  height: 60px;
}

.messages-container {
  width: 90%;
  margin: 20px auto;
}

.typing-box {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100vw;
  height: 70px;
}

input {
  outline: none;
  border-radius: 100px;
  border: 2px solid #f2efeb;
  width: 85%;
  padding: 11px;
  margin: 15px;
}

input::-webkit-input-placeholder,
.send-btn:disabled {
  color: #f2efebb2;
}

input::-moz-placeholder,
.send-btn:disabled {
  color: #f2efebb2;
}

input::-ms-input-placeholder,
.send-btn:disabled {
  color: #f2efebb2;
}

input::placeholder,
.send-btn:disabled {
  color: #f2efebb2;
}

.send-btn:disabled:hover {
  cursor: not-allowed;
  text-decoration: none;
}

.send-btn {
  -webkit-transition: all 650ms;
  -o-transition: all 650ms;
  transition: all 650ms;
  padding: 15px;
  border: none;
  font-size: 2.4rem;
}

.send-btn:hover {
  cursor: pointer;
  text-decoration: underline;
}

.fake-typing-box {
  height: 70px;
}



